<template>
  <div
    class="player-word"
    @touchstart="touchstart"
    @touchmove="touchmove"
    @touchend="touchend"
    @click="$emit('switch-word')"
  >
    <ul :style="{ marginTop: marginTop + 'px' }">
      <li
        v-for="(item, index) in lyric"
        :key="index"
        :class="{ active: active == index }"
        ref="li"
      >
        {{ item.text }}
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "PlayerWord",
  props: ["currentTime"],
  computed: {
    ...mapState(["currentmusic", "paused"]),
  },
  data() {
    return {
      lyric: "", //歌词
      marginTop: 150,
      active: 0, //歌词高亮位置
      i: 0, //当前播放到的行数
      moveY: 0, //滑动开始位置
      scrollY: 0, //滑动距离
      touching: false, //是否在触摸状态
    };
  },
  created() {
    this.getLyric();
  },
  watch: {
    currentmusic() {
      this.getLyric();
    },
    currentTime() {
      if (typeof this.lyric == "object") {
        if (!this.touching) {
          this.i = this.lyric.findIndex((element) => {
            return element.time > this.currentTime;
          });
          if (this.i < 0) {
            this.i = this.lyric.length;
          }
          this.marginTop = -40 * (this.i - 1) + 150;
          this.active = this.i - 1;
        }
      }
    },
    scrollY(n) {
      if (typeof this.lyric == "object") {
        if (this.i - 1 + n < 0 || this.i - 1 + n > this.lyric.length - 1) {
          this.scrollY = 0;
        }

        this.i = this.lyric.findIndex((element) => {
          return element.time > this.currentTime;
        });
        if (this.i < 0) {
          this.i = this.lyric.length;
        }

        this.marginTop = -40 * (this.i - 1 + n) + 150;
        this.active = this.i - 1 + n;
      }
    },
  },
  methods: {
    paresLyric(lyric) {
      var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
      var arr = lyric
        .split("\n")
        .filter((e) => e)
        .map((str) => {
          var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
          var timeArr = time.split(":");
          time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
          var text = str.replace(patt, "");
          return { time, text };
        });
      return arr;
    },
    getLyric() {
      this.axios
        .get(`/lyric?id=${this.currentmusic.id}`)
        .then((response) => {
          this.lyric = response.data.lrc.lyric;
          this.lyric = this.paresLyric(this.lyric);
          this.lyric = this.lyric.map((ele) => {
            if (!ele.text) {
              ele.text = "...";
            }
            return ele;
          });
        })
        .catch(() => {});
    },
    touchstart(e) {
      this.touching = true;
      this.moveY = e.touches[0].clientY;
    },
    touchmove(e) {
      this.scrollY = Math.floor((this.moveY - e.touches[0].clientY) / 40);
      // console.log(scrollY);
    },
    touchend() {
      this.touching = false;
      if (this.scrollY) {
        this.$emit(
          "update:currentTime",
          this.lyric[this.i - 1 + this.scrollY].time
        );
      }

      this.moveY = 0;
      this.scrollY = 0;
    },
  },
};
</script>

<style lang="less" scoped>
.player-word {
  text-align: center;
  width: 100%; //70%
  margin: 0 auto;
  height: 350px;
  overflow: hidden;
  color: #999;
  ul {
    li {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      height: 40px;
      line-height: 20px;
      width: 80vw;
      margin: 0 auto;
      &.active {
        color: white;
      }
    }
  }
}
</style>