<template>
  <div class="player-footer">
    <div class="slider">
      <div>{{ currentFormatTime }}</div>
      <van-slider
        :min="0"
        :max="duration"
        :step="1"
        :value="currentTime"
        active-color="#999"
        button-size="10px"
        @input="xxx"
      />
      <div>{{ durationFormatTime }}</div>
    </div>

    <div class="icons">
      <div class="mode" @click="$emit('update:mode')">
        <img src="@/assets/images/random.png" v-if="mode == 1" />
        <img src="@/assets/images/circulation.png" v-else-if="mode == 2" />
        <img src="@/assets/images/sequence.png" v-else />
      </div>
      <div class="prev" @click="prev">
        <img src="@/assets/images/prev.png" />
      </div>
      <div class="play" v-if="paused" @click="play">
        <img src="@/assets/images/play.png" />
      </div>
      <div class="pause" v-else @click="pause">
        <img src="@/assets/images/pause.png" />
      </div>
      <div class="next">
        <img src="@/assets/images/next.png" @click="next" />
      </div>
      <div class="list" @click="show = true">
        <img src="@/assets/images/playlist.png" />
      </div>
    </div>

    <van-popup
      v-model="show"
      position="bottom"
      :style="{ height: '60%', backgroundColor: '#3c3c3c' }"
      round
      :overlay-style="{ backgroundColor: 'transparent' }"
    >
      <div class="title">播放列表</div>
      <ul class="playlist">
        <li v-for="(song, index) in playlist" :key="song.id">
          <div
            class="info"
            :class="{ active: song.id == currentmusic.id }"
            @click="changeMusic(song, index)"
          >
            <div class="name">{{ song.name }}</div>
            <div class="split">-</div>
            <div class="author" :class="{ active: song.id == currentmusic.id }">
              <span
                v-for="(artist, index) in song.song.artists
                  ? song.song.artists
                  : song.artists"
                :key="index"
              >
                <template v-if="index"> / </template>{{ artist.name }}
              </span>
            </div>
          </div>

          <div class="delete" @click="deleteMusic(index)">
            <van-icon name="cross" />
          </div>
        </li>
      </ul>
    </van-popup>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "PlayerFooter",
  props: [
    "currentTime",
    "duration",
    "mode",
    "currentFormatTime",
    "durationFormatTime",
  ],
  computed: {
    ...mapState(["paused", "playlist", "currentmusic"]),
  },
  data() {
    return {
      show: false,
    };
  },
  methods: {
    xxx(value) {
      this.$emit("update:currentTime", value);
    },
    play() {
      this.$emit("change-play");
    },
    pause() {
      this.$emit("change-pause");
    },
    next() {
      this.$emit("next-music");
    },
    prev() {
      this.$emit("prev-music");
    },
    changeMusic(song, index) {
      this.$store.commit("updateCurrentmusic", song);
      this.$store.commit("updateCurrentindex", index);
    },
    deleteMusic(index) {
      var pl = this.playlist.concat();
      pl.splice(index, 1);
      // console.log(pl);
      // console.log(index);
      this.$store.commit("updatePlaylist", pl);

      // this.$store.commit("updateCurrentmusic", pl[index]);
      // this.$store.commit("updateCurrentindex", index);
    },
  },
};
</script>

<style lang="less" scoped>
.player-footer {
  width: 100%;
  position: fixed;
  bottom: 50px;
  .slider {
    display: flex;
    justify-content: center;
    align-items: center;
    > div {
      font-size: 14px;
      color: #ddd;
    }
    /deep/ .van-slider {
      width: 70%;
      margin: 0 10px;
    }
  }
  img {
    width: 100%;
    display: block;
  }
  .icons {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 40px;
  }
  .van-popup {
    padding: 0 10px;
    .title {
      font-size: 18px;
      border-bottom: 1px solid #ccc;
      padding: 15px 0 8px 5px;
      position: fixed;
      width: 95%;
      z-index: 10;
      background-color: #3c3c3c;
    }
    .playlist {
      margin-top: 5px;
      margin-bottom: 36px;
      &::before {
        content: "";
        display: block;
        width: 100vw;
        height: 50px;
      }
      li {
        display: flex;
        line-height: 36px;
        padding: 0 5px;

        .info {
          flex: 1;
          display: flex;
          &.active {
            color: #e51b1c;
          }
          .split {
            margin: 0 8px;
          }
          .name {
            font-size: 14px;

            word-wrap: break-word;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1; /*行数*/
            -webkit-box-orient: vertical;
          }
          .author {
            color: #999;
            font-size: 12px;
            &.active {
              color: #e51b1c;
            }

            word-wrap: break-word;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1; /*行数*/
            -webkit-box-orient: vertical;
          }
        }
        .delete {
          flex-basis: 5%;
          margin-left: 10px;
        }
      }
    }
  }
}
</style>