import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    activeid: 0, //首页推荐、推荐歌单、热歌榜
    searchs: [], //搜索结果
    currentmusic: null, //当前歌曲
    paused: true, //歌曲是否暂停
    playlist: [], //当前的播放列表
    currentindex: 0, //当前歌曲的索引值
    searchHistory: [], // 搜索历史记录
  },
  mutations: {
    updateActiveid(state, payload) {
      state.activeid = payload;
    },
    updateSearchs(state, payload) {
      state.searchs = payload;
    },
    updateCurrentmusic(state, payload) {
      state.currentmusic = payload;
    },
    updatePaused(state, payload) {
      state.paused = payload;
    },
    updatePlaylist(state, payload) {
      state.playlist = payload;
    },
    updateCurrentindex(state, payload) {
      state.currentindex = payload;
    },
    updateSearchHistory(state, payload) {
      if (state.searchHistory.includes(payload)) {
        return
      }
      state.searchHistory.unshift(payload);
    },
    resetSearchHistory(state, payload) {
      state.searchHistory = payload;
    },
  },
  actions: {
  },
  modules: {
  }
})
