<template>
  <div class="home">
    <div class="header">
      <van-nav-bar>
        <template #left>
          <van-icon name="music-o" color="#fff" @click="toRecommend" />
        </template>
        <template #title>
          <van-search
            placeholder="请输入搜索关键词"
            v-model="msg"
            class="search"
            show-action
            :clearable="false"
            @focus="toSearch"
            @input="searching"
            @cancel="clearSearch"
          />
        </template>
        <template #right>
          <div class="icon">
            <span class="playing" :class="{ paused: paused }">
              <i></i>
              <i></i>
              <i></i>
            </span>
          </div>
        </template>
      </van-nav-bar>

      <div class="nav">
        <HomeNav></HomeNav>
      </div>
    </div>

    <keep-alive>
      <router-view
        v-bind:msg.sync="msg"
        v-bind:isHot.sync="isHot"
        :image="image"
        @change-pause="pause"
        @update:image="image = $event"
      ></router-view>
    </keep-alive>

    <PlayBar
      :mode="mode"
      @prev-music="prevMusic"
      @next-music="nextMusic"
      @play-next="nextMusic"
      @update:mode="updateMode"
      ref="zz"
    ></PlayBar>
  </div>
</template>

<script>
import { mapState } from "vuex";
import PlayBar from "@/components/PlayBar.vue";
import HomeNav from "@/components/HomeNav.vue";

export default {
  name: "Home",
  components: {
    PlayBar,
    HomeNav,
  },
  data() {
    return {
      mode: 0,
      msg: "",
      isHot: true, //是否显示热搜
      image: "",
    };
  },
  computed: {
    ...mapState([
      "currentmusic",
      "paused",
      "playlist",
      "currentindex",
      "activeid",
    ]),
  },
  methods: {
    getRandom(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min);
    },
    toRecommend() {
      if (this.$route.name != "Recommend") {
        this.$router.push({ name: "Recommend" });
      }

      this.msg = "";
    },
    prevMusic() {
      let previndex;
      if (this.mode == 0) {
        if (this.currentindex > 0) {
          previndex = this.currentindex - 1;
        } else {
          previndex = this.playlist.length - 1;
        }

        this.$store.commit("updateCurrentmusic", this.playlist[previndex]);
        this.$store.commit("updateCurrentindex", previndex);
      } else if (this.mode == 1) {
        var pl = this.playlist.concat();
        pl.splice(this.currentindex, 1);
        previndex = this.getRandom(0, pl.length - 1);

        this.$store.commit("updateCurrentmusic", this.playlist[previndex]);
        this.$store.commit("updateCurrentindex", previndex);
      } else if (this.mode == 2) {
        this.$refs.zz.$refs.xx.$refs.audio.load();
      }
    },
    nextMusic() {
      let nextindex;
      if (this.mode == 0) {
        if (this.currentindex < this.playlist.length - 1) {
          nextindex = this.currentindex + 1;
        } else {
          nextindex = 0;
        }

        this.$store.commit("updateCurrentmusic", this.playlist[nextindex]);
        this.$store.commit("updateCurrentindex", nextindex);
      } else if (this.mode == 1) {
        var pl = this.playlist.concat();
        pl.splice(this.currentindex, 1);
        nextindex = this.getRandom(0, pl.length - 1);

        this.$store.commit("updateCurrentmusic", this.playlist[nextindex]);
        this.$store.commit("updateCurrentindex", nextindex);
      } else if (this.mode == 2) {
        this.$refs.zz.$refs.xx.$refs.audio.load();
      }
    },
    updateMode() {
      this.mode++;
      if (this.mode > 2) {
        this.mode = 0;
      }
    },
    toSearch() {
      if (this.msg != '') {
        this.isHot = false;
      }
      if (this.$route.name != "Search") {
        this.$router.push({ name: "Search" });
      }
      
    },
    searching() {
      this.isHot = false;
    },
    clearSearch() {
      this.isHot = true;
      if (this.$route.name != "Search") {
        this.$router.push({ name: "Search" });
      }
    },
    pause() {
      this.$refs.zz.$refs.xx.$refs.audio.pause();
    },
  },
};
</script>

