<template>
  <div class="player">
    <audio
      :src="'https://music.163.com/song/media/outer/url?id=' + id + '.mp3'"
      autoplay
      ref="audio"
    ></audio>
    <!-- controls
      style="width:100%;height:40px" -->

    <div class="customized" v-if="currentmusic" @click="toggleShow">
      <img
        :src="
          currentmusic.picUrl ? currentmusic.picUrl : currentmusic.al.picUrl
        "
        class="empty pic"
        :class="{ paused: paused }"
      />
      <div class="info">
        <div class="name">{{ currentmusic.name }}</div>

        <div class="author">
          <span
            v-for="(artist, index) in currentmusic.song.artists"
            :key="index"
          >
            <template v-if="index"> / </template>{{ artist.name }}
          </span>
        </div>
      </div>
      <div class="time">
        {{ currentFormatTime }}<span>/</span>{{ durationFormatTime }}
      </div>
      <div class="control" @click.stop="togglePlayState">
        <canvas ref="circle" width="50" height="50"></canvas>

        <span class="icon" v-if="paused">
          <van-icon name="play" color="#000" />
        </span>
        <span class="icon" v-else>
          <van-icon name="pause" color="#000" />
        </span>
      </div>
      <!-- <div class="more">
        <img src="../assets/images/icon2.png" />
      </div> -->
    </div>
    <div class="customized" v-else @click="toggleShow">
      <div class="empty"><img src="../assets/images/empty.png" /></div>
      <div class="info">
        <div class="name">未在播放</div>
        <div class="author"></div>
      </div>
      <div class="time">
        {{ currentFormatTime }}<span>/</span>{{ durationFormatTime }}
      </div>
      <div class="control">
        <canvas ref="circle" width="50" height="50"></canvas>

        <span class="icon" v-if="paused">
          <van-icon name="play" color="#000" />
        </span>
        <span class="icon" v-else>
          <van-icon name="pause" color="#000" />
        </span>
      </div>
      <!-- <div class="more">
        <img src="../assets/images/icon2.png" />
      </div> -->
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Player",
  props: ["show", "mode", "currentFormatTime", "durationFormatTime"],
  data: function () {
    return {
      duration: 0,
      currentTime: 0,
    };
  },
  computed: {
    ...mapState(["currentmusic", "paused", "playlist", "currentindex"]),
    id() {
      if (this.currentmusic) {
        return this.currentmusic.id;
      } else {
        return "";
      }
    },
  },
  mounted() {
    this.drawCircle(this.currentTime, this.duration);

    this.$refs.audio.addEventListener("pause", () => {
      // this.paused = true;
      this.$store.commit("updatePaused", true);
    });
    this.$refs.audio.addEventListener("play", () => {
      // this.paused = false;
      this.$store.commit("updatePaused", false);
    });

    this.$refs.audio.addEventListener("durationchange", () => {
      this.duration = this.$refs.audio.duration;
    });

    this.$refs.audio.addEventListener("timeupdate", () => {
      this.currentTime = this.$refs.audio.currentTime;

      this.drawCircle(this.currentTime, this.duration);
    });

    this.$refs.audio.addEventListener("ended", () => {
      this.playNext();
    });
  },
  watch: {
    duration: function (newVal) {
      this.$emit("update:duration", newVal);

      this.$emit("update:durationFormatTime", this.formatTime(this.duration));
    },
    currentTime: function (newVal) {
      this.$emit("update:currentTime", newVal);

      this.$emit("update:currentFormatTime", this.formatTime(this.currentTime));
    },
  },
  methods: {
    formatTime(time) {
      var minute = Math.floor(time / 60);
      minute = minute < 10 ? "0" + minute : minute;
      var second = Math.floor(time % 60);
      second = second < 10 ? "0" + second : second;
      return `${minute}:${second}`;
    },
    drawCircle: function (n, total) {
      let canvas = this.$refs.circle;
      let ctx = canvas.getContext("2d");

      ctx.clearRect(0, 0, 50, 50);
      ctx.beginPath();
      ctx.lineWidth = 3;
      ctx.strokeStyle = "rgba(0, 0, 0, 0.3)";
      ctx.arc(25, 25, 16, 0, Math.PI * 2, false); // 绘制
      ctx.stroke();
      ctx.closePath();

      ctx.beginPath();
      ctx.strokeStyle = "rgba(75,35,183, 0.6)";
      ctx.arc(
        25,
        25,
        15,
        Math.PI * -0.5,
        Math.PI * ((n / total) * 2 - 0.5),
        false
      );
      ctx.stroke();
      ctx.closePath();
    },
    //播放、暂停
    togglePlayState: function () {
      let audio = this.$refs.audio;
      if (this.paused) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    toggleShow: function () {
      if (this.currentmusic) {
        this.$emit("update:show", !this.show);
      }
    },
    playNext: function () {
      this.$emit("play-next");
    },
  },
};
</script>

<style lang="less" scoped>
.player {
  position: fixed;
  width: 100%;
  bottom: 0;
  audio {
    display: block;
  }

  .customized {
    font-size: 14px;
    width: 100%;
    height: 50px;
    background-color: #f4f4f4;
    display: flex;
    align-items: center;
    .empty {
      width: 40px;
      height: 40px;
      background-color: white;
      display: inline-block;
      border-radius: 50%;
      box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
      margin-left: 13px;
      position: relative;
      img {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
      }
    }
    .pic {
      animation: playing 8s linear infinite;
    }
    .paused {
      animation-play-state: paused;
    }
    .info {
      flex: 1;
      margin-left: 20px;
      line-height: 20px;
      .name,
      .author {
        word-wrap: break-word;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1; /*行数*/
        -webkit-box-orient: vertical;
      }
      .name {
        // color: #000;
        color: #4b23b7;
        font-size: 14px;
      }
      .author {
        font-size: 13px;
        color: #999;
      }
    }
    .time {
      margin-right: 2px;
      color: #000;
      span {
        margin: 0 2px;
      }
    }
    .control {
      margin-right: 10px;
      position: relative;
      .icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
        /deep/ .van-icon {
          font-size: 18px;
        }
      }
    }
    .more {
      width: 30px;
      height: 30px;
      margin-right: 10px;
      display: flex;
      align-items: center;
      img {
        display: block;
        width: 100%;
      }
    }
  }
}

@keyframes playing {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>