<template>
  <div class="play-bar">
    <Player
      v-bind:currentFormatTime.sync="currentFormatTime"
      v-bind:durationFormatTime.sync="durationFormatTime"
      @play-next="$emit('play-next')"
      v-bind:show.sync="show"
      v-bind:duration.sync="duration"
      v-bind:currentTime.sync="currentTime"
      :mode="mode"
      ref="xx"
    ></Player>

    <div class="music">
      <van-popup
        closeable
        close-icon="arrow-down"
        close-icon-position="top-left"
        v-model="show"
        position="bottom"
        :style="{ height: '100%' }"
        duration="0.25"
      >
        <PlayerHeader></PlayerHeader>

        <PlayerContent
          v-show="!isWord"
          @switch-word="switchWord"
        ></PlayerContent>
        <PlayerWord
          :currentTime="currentTime"
          :isWord="isWord"
          v-show="isWord"
          @update:currentTime="changeCurrentTime"
          @switch-word="switchWord"
        ></PlayerWord>

        <PlayerFooter
          v-bind:currentTime="currentTime"
          v-bind:duration="duration"
          :mode="mode"
          :isWord="isWord"
          :currentFormatTime="currentFormatTime"
          :durationFormatTime="durationFormatTime"
          @update:currentTime="changeCurrentTime"
          @change-play="play"
          @change-pause="pause"
          @prev-music="$emit('prev-music')"
          @next-music="$emit('next-music')"
          @switch-word="switchWord"
          @update:mode="$emit('update:mode')"
        ></PlayerFooter>
      </van-popup>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Player from "@/components/Player.vue";
import PlayerHeader from "@/components/PlayerHeader.vue";
import PlayerContent from "@/components/PlayerContent.vue";
import PlayerWord from "@/components/PlayerWord.vue";
import PlayerFooter from "@/components/PlayerFooter.vue";

export default {
  name: "PlayBar",
  components: {
    Player,
    PlayerHeader,
    PlayerContent,
    PlayerFooter,
    PlayerWord,
  },
  props: ["mode"],
  computed: {
    ...mapState(["currentmusic", "paused", "playlist", "currentindex"]),
  },
  data() {
    return {
      show: false, //是否显示歌曲详情页面
      duration: 0, //歌曲持续时间
      currentTime: 0, //当前播放时间
      isWord: false, //是否显示歌词
      currentFormatTime: "00:00",
      durationFormatTime: "00:00",
    };
  },
  methods: {
    changeCurrentTime(n) {
      this.$refs.xx.$refs.audio.currentTime = n;
    },
    play() {
      this.$refs.xx.$refs.audio.play();
    },
    pause() {
      this.$refs.xx.$refs.audio.pause();
    },
    switchWord() {
      this.isWord = !this.isWord;
    },
  },
};
</script>

<style lang="less" scoped>
.play-bar {
  color: #fff;
  width: 100%;

  .music {
    /deep/ .van-popup {
      background-color: #474d4f;
      .van-popup__close-icon {
        font-size: 30px;
      }
    }
  }
}
</style>