<template>
  <div class="player-content" @click="$emit('switch-word')">
    <div class="circle">
      <div class="bg">
        <div class="pic">
          <img
            :src="
              currentmusic.picUrl ? currentmusic.picUrl : currentmusic.al.picUrl
            "
            class="play"
            :class="{ paused: paused }"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "PlayerContent",
  computed: {
    ...mapState(["currentmusic", "paused"]),
  },
};
</script>

<style lang="less" scoped>
.player-content {
  width: 100%;
  height: 350px;
  display: flex;
  justify-content: center;
  .circle {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    background-color: rgba(63, 63, 63, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    .bg {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 225px;
      height: 225px;
      border-radius: 50%;
      background-color: rgba(28, 28, 28, 0.8);

      .pic {
        width: 130px;
        height: 130px;
        border-radius: 50%;
        overflow: hidden;
        box-shadow: 0 0 8px 0 rgba(241, 241, 241, 0.8);
        img {
          display: block;
          width: 100%;
          &.play {
            animation: playing 25s linear infinite;
          }
          &.paused {
            animation-play-state: paused;
          }
        }
      }
    }
  }
}

@keyframes playing {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>