<template>
  <ul id="home-nav">
    <li
      v-for="item in titles"
      :key="item.id"
      @click="changeActive(item.id, item.path)"
    >
      <span :class="{ active: activeid == item.id }">{{ item.name }}</span>
    </li>
  </ul>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "HomeNav",
  computed: {
    ...mapState(["activeid"]),
  },
  data() {
    return {
      active: this.activeid,
      titles: [
        {
          id: 0,
          name: "首页推荐",
          path: "Recommend",
        },
        {
          id: 1,
          name: "推荐歌单",
          path: "List",
        },
        {
          id: 2,
          name: "热歌榜",
          path: "Rank",
        },
      ],
    };
  },
  methods: {
    changeActive(id, path) {
      this.$store.commit("updateActiveid", id);
      this.$router.push({name: path});
    },
  },
};
</script>

<style lang="less" scoped>
#home-nav {
  font-size: 14px;
  display: flex;
  text-align: center;
  height: 40px;
  .active {
    color: #4d32b4;
    border-bottom: 2px solid #4d32b4;
  }
  li {
    flex: 1;
    span {
      display: inline-block;
      line-height: 38px;
      color: black;
      border-bottom: 2px solid transparent;
      padding: 0 10px;
    }
  }
}
</style>